import React, { Component } from 'react'
import { Button } from 'antd'
import { PRICE_LIST } from '@a/js/enume'
import { uniqBy } from 'lodash'
import './style.less'

export default class ClothingCommodity extends Component {
  constructor(props){
    super(props)
    this.state = {
      sizeList: props.sizeList,
      list: props.list || []
    }
  }
  componentDidMount () {
    this.props.onRef(this)
  }
  componentDidUpdate (prevProps, prevState) {
    if (JSON.stringify(this.props.list) !== JSON.stringify(prevProps.list)) {
      this.setState({
        list: this.props.list
      })
    }
  }
  UNSAFE_componentWillReceiveProps (prevProps, prevState) {
    this.setState({
      sizeList: prevProps.sizeList
    })
  }
  render() {
    const { list } = this.state
    return (
      <div className='commodity-info'>
        <div>
          <div>
            { list.length } Product(s) found.
          </div>
          <div>
            Order by&nbsp;
            <select className='commodity-sort' onChange={this.onSelect}>
              {
               PRICE_LIST.map((opt, i)=>(
                  <option key={i} value={opt.type}>{opt.name}</option>
                ))
              }
            </select>
          </div>
        </div>
        <div className='commodity-list'>
            {
              list.length ? this.renderItem(list) : null
            }
        </div>
      </div>
    )
  }
  renderItem=(list)=>{
    return list.map((opt, i)=>(
      <div className='item-box' key={opt.id}>
        <div>
          { opt.isFreeShipping ? <span className='welfare-info'>Free Shipping</span> :  null }
          <img src={require(`@a/img/${opt.sku}_1.jpg`)} alt="" />
        </div>
        <div>
          <p className='commodity-title'>{opt.title}</p>
          <div className='commodity-money'>
            <span>{opt.currencyFormat} {opt.price}</span>
            <span>or {opt.installments}x${(opt.price/opt.installments).toFixed(2)}</span>
          </div>
          <div className='commodity-extend'>{opt.extendInfo}</div>
        </div>
        <div className='commodity-add'>
          <Button onClick={()=>this.onAdd(opt)}>Add to cart</Button>
        </div>
      </div>
    ))
  }
  // 
  onSelect=(e)=>{
    let n = !e ? '1' : ''
    let { list, sizeList } = this.state
    e && e.target.childNodes.forEach(opt=>{
      if(opt.selected) n = opt.value
    })
    if(n === '2') list = list.sort((a, b)=> a.price - b.price )
    else if(n === '3') list = list.sort((a, b)=> b.price - a.price )
    else if(n === '1') list = list.concat(this.props.list).filter(opt =>
      sizeList.length ? sizeList.some(item => opt.availableSizes.includes(item)) : true)
    this.setState({ list: uniqBy([...new Set(list)], 'id') })
  }
  // 
  onAdd=(obj)=>{
    const { add } = this.props
    add(obj, 'id')
  }
}
