import api from '@/services'

export default {
  namespace: 'product',
  state: {
    productList: []
  },
  effects: {
    *getProductList ({ payload }, { call, put }) {
      const data = yield call(api.getProductList, payload)
      yield put({ type: 'save', payload: { productList: data.products } })
    }
  },
  reducers: {
    save (state, action) {
      return { ...state, ...action.payload }
    }
  }
}
