
import { Component } from 'react';
import './index.less';
import Size from '@c/size'
import Product from '@c/product'
import { Badge } from 'antd'
import { ShoppingCartOutlined } from '@ant-design/icons'
import { connect } from 'dva'
import ShoppingCart from '@c/shoppingCart'
import { countToal } from '@u/common'
class IndexPage extends Component {
  constructor(props){
    super(props)
    this.state={
      cartList: props.cartList || [], //
      cartShow: false,
      sizeList: [], //
      star: 1
    }
  }
  componentDidMount(){
    this.props.dispatch({ type: 'product/getProductList' })
    if(localStorage.cartList){
      this.setState({ cartList: JSON.parse(localStorage.cartList) })
    }
  }
  render(){
    let { cartList, cartShow, star, sizeList } = this.state
    let { productList } = this.props
    return (
      <article className='shopping-list'>
        <div className='left-cotent'></div>
        <div className='commodity-box'>
          <Size callBack={this.onChangeSize} star={star} staradd={this.staradd}/>
          <Product add={this.addToCart} sizeList={sizeList} list={productList} onRef={ref => { this.product = ref }}/>
        </div>
        <div className='shopping-car'>
          {
            !cartShow ? (
              <div className='show-icon'>
                <Badge count={countToal(cartList)}>
                <ShoppingCartOutlined onClick={()=>this.setState({ cartShow: true })} />
                </Badge>
              </div>
            ) : (
              <div>
                <ShoppingCart list={cartList} update={(cartList)=> this.setState({ cartList })} close={()=> this.setState({ cartShow: false })} dispatch={this.props.dispatch}/>
              </div>
            )
          }
        </div>
      </article>
    )
  }
  // 
  staradd=()=>{
    let { star } = this.state
    this.setState({ star: ++star })
  }
  //
  onChangeSize=(param)=>{
    this.props.dispatch({ type: 'product/getProductList' })
    this.setState({ sizeList: param }, this.product.onSelect)
  }
  //
  addToCart=(item, key)=>{
    let { cartList } = this.state
    if(!item) return void(0)
    if(cartList.every(opt=> opt[key] !== item[key])) cartList.push({ ...item, count: 1 })
    else cartList.length && cartList.forEach(opt=>{
      if(opt[key] === item[key]) opt.count = opt.count+1
    })
    this.props.dispatch({ type: 'cart/saveCartList', payload: cartList })
    this.setState({ cartList })
  }
}
export default connect(({ product, cart }) => ({
  productList: product.productList,
  cartList: cart.cartList
}))(IndexPage)
