import api from '@/services'

export default {
  namespace: 'cart',
  state: {
    cartList: []
  },
  effects: {
    *saveCartList ({ payload }, { call, put }) {
      localStorage.cartList = JSON.stringify(payload)
      yield put({ type: 'save', payload: { cartList: payload } })
    }
  },
  reducers: {
    save (state, action) {
      return { ...state, ...action.payload }
    }
  }
}
