import React, { Component } from 'react'
import { ShoppingCartOutlined, CloseOutlined, PlusOutlined, MinusOutlined } from '@ant-design/icons'
import { Button, Badge, Modal } from 'antd'
import { countToal } from '@u/common'
import './style.less'

export default class ShoppingCarBox  extends Component {
  constructor(props){
    super(props)
    this.state={
      commodityList: props.list, //
      visible: false //
    }
  }
  render() {
    const { commodityList, visible } = this.state
    const { close } = this.props
    return (
      <div className='shopping-car-box'>
        <div>
          <span>
            <CloseOutlined onClick={close}></CloseOutlined>
          </span>
          <div>
            <Badge count={countToal(commodityList)}>
              <ShoppingCartOutlined />
            </Badge>
            <span style={{ fontSize: '18px' }}>Cart</span>
          </div>
        </div>
        <div className='car-box'>
          <div>
            {
              commodityList.map((opt, i)=>(
                <div key={opt.id}>
                  <div>
                    <img src={require(`@a/img/${opt.sku}_1.jpg`)} alt='' />
                  </div>
                  <div>
                    <div><CloseOutlined onClick={()=>this.onDeleteItem(opt.id)}></CloseOutlined></div>
                    <p>
                      <span>{opt.title}</span>
                      <span>{opt.currencyFormat} {opt.price}</span>
                    </p>
                    <p>
                      <span>{opt.type}</span>
                      <span>{opt.availableSizes[0]} | {opt.style}</span>
                    </p>
                    <p>
                      <span>quantity: {opt.count}</span>
                      <span>
                        <Button icon={<MinusOutlined />} disabled={opt.count <= 1} onClick={()=>this.onItemChange(true, opt.id)}></Button>
                        <Button icon={<PlusOutlined />} onClick={()=>this.onItemChange(false, opt.id)}></Button>
                      </span>
                    </p>
                  </div>
                </div>
              ))
            }
          </div>
          <div className='total-number'>
            <div>
              SUBTOTAL
            </div>
            <div>
              <span>$ {this.countNumber().toFixed(2)}</span>

            </div>

          </div>
          <div className='car-submit'>
            <Button onClick={()=>{
              this.setState({
                visible: true
              })
            }}>CHECKOUT</Button>
          </div>
        </div>
        <Modal
          title="CHECKOUT"
          visible={visible}
          onOk={()=>{
            this.setState({
              visible: false
            })
          }}
          onCancel={()=>{
            this.setState({
              visible: false
            })
          }}
        >
          <p>{`Checkout - Subtotal: $ ${this.countNumber()}`}</p>
        </Modal>
      </div>
    )
  }
  onItemChange=(Is, key)=>{
    let { commodityList } = this.state
    commodityList.forEach((opt) => {
      if(opt.id === key) opt.count = Is ? ( opt.count - 1 ) : ( opt.count + 1 )
    })
    this.cacheData(commodityList)
    this.setState({ commodityList })
  }
  onDeleteItem=(id)=>{
    let { commodityList } = this.state
    commodityList = commodityList.filter(opt=> opt.id !== id)
    this.cacheData(commodityList)
    this.setState({ commodityList })
  }
  cacheData=(data)=>{
    const { dispatch, update } = this.props
    update(data)
    dispatch({ type: 'cart/saveCartList', payload: data })
    // localStorage.cartList = JSON.stringify(data)
  }
  countNumber=()=>{
    const { commodityList } = this.state
    return commodityList.reduce((total, opt)=>{
      return total += opt.count * Number(opt.price)
    }, 0)
  }
}
